<template>
    <div class="region-box">
        <el-switch v-model="regionVisible" @change="switchRegionVisible" active-text="显示" inactive-text="隐藏"></el-switch>
        <div id="popul-box">
            <div id="popul" v-show="false">
                <span class="handle-icon">
                    <span>
                        <i class="el-icon-edit" title="编辑图形"></i>
                        <i class="el-icon-search" title="查看要素信息"></i>
                    </span>
                    <i class="el-icon-error" title="关闭" @click="closeOverlayer()"></i>
                </span>
                <span v-if="previewFeature && previewFeature.values_">名称：{{previewFeature.values_.name}}</span>
                <span v-if="previewFeature && previewFeature.values_">code：{{previewFeature.values_.adcode}}</span>
            </div>
        </div>  
    </div>
</template>
<script>
import Overlay from 'ol/Overlay.js';
import { getMapData } from "@/httpService/api.js"
import { regionLayer } from "@/mapConfig/mapVectorLayers.js"
import { geojsonToFeatures } from "../mapJs/loadGeoJsonData.js"
import { modifyDefaultStyle, activeDefaultStyle } from "../mapJs/setVectorStyle"

export default ({
    data() {
        return {
            map: null,
            regionLayer: null,
            regionSource: null,
            regionVisible: true,
            previewFeature: null,
            popup: null,
        }   
    },
    mounted () {
        let that = this;
        that.$nextTick(() => {
            that.map = window.olMap;
            that.regionLayer =  regionLayer;
            that.regionSource = that.regionLayer.getSource();
            that.regionLayer.setStyle(modifyDefaultStyle)
            that.getChinaMapData()
            that.map.on('click', that.handleClickMap) 
        })
    },
    beforeDestroy(){
        this.regionSource.clear()
        this.closeOverlayer();
    },
    methods: {
        getChinaMapData(){
            let that = this;
            getMapData().then(res => {
                if (res && res.status === 200 && res.data) {
                    let features = geojsonToFeatures(res.data);
                    features.map(f => {
                        f.set("layer_name", "region")
                    })
                    that.regionSource.addFeatures(features)
                }
            })
        },
        switchRegionVisible(val){
            let that = this;
            that.regionLayer.setVisible(val)
            if (!val) {
                that.closeOverlayer(); 
            }
        },
        handleClickMap (event){
            let that = this;
            let coordinate = event.coordinate;
            let feature = that.map.forEachFeatureAtPixel(event.pixel, function (feature) {
                return feature;
            });
            if (feature && feature.values_ && feature.values_.layer_name === 'region') {
                if (that.previewFeature) {
                    that.previewFeature.setStyle(null)
                }
                that.previewFeature = feature;
                that.previewFeature.setStyle(activeDefaultStyle)
                that.createOverLayer(feature, coordinate)
            }     
        },
        closeOverlayer(){
            let that = this;
            if (!that.popup) return;
            let popul = document.getElementById("popul")
            popul.style.display = "none";
            document.getElementById("popul-box").appendChild(popul)
            that.map.removeOverlay(that.popup);
            that.popup = null;
            if (that.previewFeature) {
                that.previewFeature.setStyle(null)
            }
            that.previewFeature = null;
        },
        createOverLayer(feature, coordinate){
            let that = this;
            let box = document.createElement('div');
            box.className = "tooltip-content"
            let popul = document.getElementById("popul")
            popul.style.display = "flex";
            box.appendChild(popul)
            that.popup = new Overlay({
                element: box,
                id: feature.ol_uid,
                offset: [0, -10],
                autoPan: true,
                positioning: 'bottom-center',
            });
            that.popup.setPosition(coordinate);
            that.map.addOverlay(that.popup);
        },
    }
})
</script>
